export default (sequelize, Sequelize) => {
  const MajorNew = sequelize.define("majorNew", {
    name_th: {
      type: Sequelize.STRING,
    },
    name_en: {
      type: Sequelize.STRING,
    },
    note_th: {
      type: Sequelize.STRING,
    },
    note_en: {
      type: Sequelize.STRING,
    },
    chance_th: {
      type: Sequelize.STRING,
    },
    chance_en: {
      type: Sequelize.STRING,
    },
  });
  return MajorNew;
};
